@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;  
  overflow: hidden
}
body{
  background-color: #0F172A;
  color: white;
  font-family: 'Roboto', sans-serif;
}

.game {
  display: grid;
  grid-template-columns: 100vw;
  grid-template-rows: 60vh 30vh;
  place-items: center;
}

.board {
  grid-column-start: 1;
  grid-template-rows: 1;
  display: flex;
  flex-direction: column;
  margin-top: 1.5em;
}
.row {
  display: flex;
  flex-direction: row;
}
.letter{
  border-radius: 3px;  
  border: 1px solid #475569;
  width: 2em;
  height: 2em;
  margin: 0.2em;
  display: grid;
  place-items: center;
  grid-template-columns: 5;
  grid-template-rows: 8;
  font-size: 1.4rem;
  font-weight: bolder;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}
.keyboard {
  grid-column-start: 1;
  grid-template-rows: 2;
  width: 98vw;
}
.line1, .line2, .line3, .line4{
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
}
.key {
  text-transform: uppercase;
  width: 4em;
  height: 2em;
  margin: 0.1em;
  border-radius: 3px;
  display: grid;
  place-items: center;
  font-size: 22px;
  background-color: #475569;
  color: white;
  cursor: pointer;
}
#big{
  width: 10em;
  padding: 0;
}
@media only screen and (min-width: 600px){
  #big{
    width: fit-content;
    padding: 1em;
  }
}
#disabled{
  background-color: #1E293B;
}
#correct {
  background-color: #5BC560;
}

#almost {
  background-color: #EAB441;
}

#error {
  background-color: #334155;
}
.gameOver{
  /* margin-top: 1em; */
  display: flex;
  align-items: center;
  flex-direction: column;
}
